const validate = require('validate.js');

const {
    addTag,
    addIndices,
    addCustomer,
    addReminder,
    ElarianMessages,
    addOtherCustomer,
    addCustomerNumber,
    addCustomerIndices,
    getChannelProvider,
    getValidCollectionKeys,
    getCustomerNumberProviderString,
    getChannelProviderString,
} = require('./utils');

const {
    DataMapValue,

    AppToServerCommand,
    AppToServerCommandReply,

    PaymentChannel,
    ActivityChannel,
    MessagingChannel,
    CustomerNumberProvider,
    ActivityChannelNumber,
    CustomerActivityCommand,
    GetCustomerStateCommand,
    DeleteCustomerTagCommand,
    UpdateCustomerTagCommand,
    AdoptCustomerStateCommand,
    AddCustomerReminderCommand,
    LeaseCustomerAppDataCommand,
    DeleteCustomerAppDataCommand,
    DeleteCustomerMetadataCommand,
    UpdateCustomerAppDataCommand,
    CancelCustomerReminderCommand,
    AddCustomerReminderTagCommand,
    UpdateCustomerMetadataCommand,
    DeleteCustomerSecondaryIdCommand,
    UpdateCustomerSecondaryIdCommand,
    CancelCustomerReminderTagCommand,

} = ElarianMessages;

let customerOptions;

/**
 * A customer is your end-user, represented by a number (from a cellular, facebook or telegram)
 * @constructor
 * @param {CustomerParams} params
 */
function Customer(params) {
    const constraints = {
        customerId: {
            type: 'string',
        },
        'customerNumber.number': {
            type: 'string',
        },
        'customerNumber.provider': {
            type: 'string',
            inclusion: getValidCollectionKeys(CustomerNumberProvider),
        },
        'customerNumber.partition': {
            type: 'string',
        },
    };

    const error = validate(params, constraints);
    if (error) {
        let message = '';
        Object.keys(error).forEach((key) => {
            message += `${key}: ${error[key].join(',')}\n`;
        });
        throw new Error(message);
    }

    this.customerId = params.customerId;
    this.customerNumber = params.customerNumber;

    this.identityState = {};
    this.messagingState = {};
    this.paymentState = {};
    this.activityState = {};

    /* eslint-disable no-use-before-define */
    /**
     * Fetch the customer's current state.
     * @returns {CustomerState}
     */
    this.getState = async () => {
        const data = await getCustomerState(this);
        this.customerId = data.customerId;
        const numbers = [
            ...(data.messagingState ? data.messagingState.channels : [])
                .map((i) => Object.keys(i)
                    .map((ki) => (i[ki] ? i[ki].customerNumber : null))).flat(),
            ...(data.paymentState ? data.paymentState.customerNumbers : []).map((i) => ({ ...i, state: 'payment' })),
        ];
        if (numbers.length && !this.customerNumber) {
            const candidate = numbers.find((i) => i.number);
            this.customerNumber = candidate;
        }

        this.identityState = data.identityState;
        this.paymentState = data.paymentState;
        this.messagingState = data.messagingState;
        this.activityState = data.activityState;

        return data;
    };

    /**
     * Merge otherCustomer's state into this customer's state and discard otherCustomer
     * @param {Customer} otherCustomer
     * @returns {UpdateStatus}
     */
    this.adoptState = async (otherCustomer) => adoptCustomerState(this, otherCustomer);

    /**
     * Update a customer's tag list.
     * @param {Tag[]} tags
     * @returns {UpdateStatus}
     */
    this.updateTag = async (tags) => updateCustomerTag(this, tags);

    /**
     * Remove some tags from a customer
     * @param {Tag[]} tags
     * @returns {UpdateStatus}
     */
    this.deleteTag = async (tags) => deleteCustomerTag(this, tags);

    /**
     * Update a customer's secondary Ids
     * @param {SecondaryId[]} secondaryIds
     * @returns {UpdateStatus}
     */
    this.updateSecondaryId = async (secondaryIds) => updateCustomerSecondaryId(this, secondaryIds);

    /**
     * Remove some secondary Ids from a customer
     * @param {SecondaryId[]} secondaryIds
     * @returns {UpdateStatus}
     */
    this.deleteSecondaryId = async (secondaryIds) => deleteCustomerSecondaryId(this, secondaryIds);

    /**
     * Set a reminder to be triggered at the specified time for a particular customer
     * @param {Reminder} reminder
     * @returns {UpdateStatus}
     */
    this.addReminder = async (reminder) => addCustomerReminder(this, reminder);

    /**
     * Cancels a previously set reminder with the key <code>key</code> on the customer
     * @param {string} key
     * @returns {UpdateStatus}
     */
    this.cancelReminder = async (key) => cancelCustomerReminder(this, key);

    /**
     * Sets some metadata on the customer.
     * Values in the metadata object can either be strings or buffers,
     * depending on the set serializer
     * @param {Object} metadata
     * @returns {UpdateStatus}
     */
    this.updateMetadata = async (metadata) => updateCustomerMetadata(this, metadata);

    /**
     * Remove some metadata from a customer. <code>keys</code> is an array of strings
     * @param {string[]} keys
     * @returns {UpdateStatus}
     */
    this.deleteMetadata = async (keys) => deleteCustomerMetadata(this, keys);

    /**
     * Sets some app data on the customer.
     * Values in the data object can either be strings or buffers,
     * depending on the set serializer
     * @param {Object} data
     * @returns {UpdateStatus}
     */
    this.updateAppData = async (data) => updateCustomerAppData(this, data);

    /**
     *  Fetches the customer's app data and lock it from fetching(for up to <b>90s</b>)
     *  until next call to update app data.
     * @returns {LeasedAppData}
     */
    this.leaseAppData = async () => leaseCustomerAppData(this);

    /**
     * Remove customer's app data
     * @returns {UpdateStatus}
     */
    this.deleteAppData = async () => deleteCustomerAppData(this);

    /**
     * Send a message to the customer from the specified channel number.
     * @param {MessagingChannelNumber} channelNumber
     * @param {Body} body
     * @returns {MessageStatus}
     */
    // eslint-disable-next-line max-len
    this.sendMessage = async (channelNumber, body) => customerOptions.client.sendMessage(this, channelNumber, body);

    /**
     * Initiate a voice call to the customer
     * @param {string} channelNumber
     * @param {VoiceAction[]} actions
     * @returns {MessageStatus}
     */
    // eslint-disable-next-line max-len
    this.makeVoiceCall = async (channelNumber, actions = []) => customerOptions.client.makeVoiceCall(this, channelNumber, actions);

    /**
     * Allow or block a customer from receiving messages from a channel
     * @param {Customer} customer
     * @param {MessagingChannelNumber} channelNumber
     * @param {string} action allow or block
     * @returns {ConsentStatus}
     */
    // eslint-disable-next-line max-len
    this.updateMessagingConsent = async (channelNumber, action) => customerOptions.client.updateMessagingConsent(this, channelNumber, action);

    /**
     * Initiate a customer activity
     * @param {ActivityChannelNumber} channelNumber
     * @param {string} sessionId
     * @param {string} key
     * @param {Object} properties
     * @returns {UpdateStatus}
     */
    // eslint-disable-next-line max-len
    this.updateActivity = async (channelNumber, sessionId, key, properties = {}) => updateCustomerActivity(this.customerNumber, channelNumber, sessionId, key, properties);
    /* eslint-enable no-use-before-define */

    /**
     * Initiate a payment transaction
     * @param {PaymentChannelNumber|Wallet} source
     * @param {Wallet|Purse} destination
     * @param {Cash} value
     * @returns {PaymentStatus}
     * @memberof Client.prototype
     */
    this.requestPayment = async (source, destination, value) => {
        if (!this.customerId && (source.walletId || destination.walletId)) {
            const state = await this.getState();
            this.customerId = state.customerId;
        }

        let debitParty = source;
        if (debitParty.number) {
            debitParty = {
                customerNumber: this.customerNumber,
                channelNumber: source,
            };
        } else if (debitParty.walletId) {
            debitParty.customerId = this.customerId;
        }

        const creditParty = destination;
        if (creditParty.walletId) {
            creditParty.customerId = this.customerId;
        }
        return customerOptions.client.initiatePayment(debitParty, creditParty, value);
    };
}

/**
 * Fetch the customer's current state.
 * @param {Customer} customer
 * @returns {CustomerState}
 * @memberof Elarian.prototype
 */
const getCustomerState = (customer) => {
    const {
        getSocket,
    } = customerOptions;

    if (!(customer instanceof Customer)) {
        throw new Error('Invalid customer');
    }

    const socket = getSocket();

    let cmd = new GetCustomerStateCommand();
    cmd = addCustomer(cmd, customer);
    const req = new AppToServerCommand()
        .setGetCustomerState(cmd);

    return new Promise((resolve, reject) => {
        socket
            .requestResponse({
                data: Buffer.from(req.serializeBinary()),
            })
            .subscribe({
                onComplete: (value) => {
                    try {
                        const res = AppToServerCommandReply
                            .deserializeBinary(value.data)
                            .getGetCustomerState();

                        const result = {
                            status: res.getStatus(),
                            description: res.getDescription(),
                            data: (res.getData() || {
                                toObject: () => undefined,
                            }).toObject(),
                        };

                        if (!result.status) {
                            throw new Error(result.description);
                        }

                        // TODO: Clean up paymentState, messagingState, identityState

                        // eslint-disable-next-line max-len
                        if (result.data.identityState && Object.keys(result.data.identityState).length) {
                            const { serializer: { type, deserialize } } = customerOptions;
                            const deserializeKey = type === 'text' ? 'stringVal' : 'bytesVal';
                            const unprocessedState = result.data.identityState;
                            const identityState = {
                                tags: unprocessedState.tagsList.map((i) => ({
                                    key: i.mapping.key,
                                    value: i.mapping.value.value,
                                    expiration: i.expiresAt.seconds,
                                })),
                                secondaryIds: unprocessedState.secondaryIdsList.map((i) => ({
                                    key: i.mapping.key,
                                    value: i.mapping.value.value,
                                    expiration: i.expiresAt.seconds,
                                })),
                                metadata: Object.assign(
                                    {},
                                    ...unprocessedState.metadataMap.map((i) => ({
                                        [i[0]]: deserialize(i[1][deserializeKey]),
                                    })),
                                ),
                            };
                            result.data.identityState = identityState;
                        }

                        if (result.data.paymentState) {
                            const entry = result.data.paymentState;
                            result.data.paymentState = {
                                wallets: entry.walletsMap,
                                transactionLogs: entry.transactionLogList,
                                pendingTransactions: entry.pendingTransactionsList,
                                customerNumbers: entry.customerNumbersList.map((i) => ({
                                    number: i.number,
                                    provider: getCustomerNumberProviderString(i.provider),
                                })),
                                channelNumbers: entry.channelNumbersList.map((i) => ({
                                    number: i.number,
                                    channel: getChannelProviderString(i.channel, PaymentChannel),
                                })),
                            };
                        }

                        if (result.data.messagingState) {
                            result.data.messagingState = {
                                channels: result.data.messagingState.channelsList.map((ch) => {
                                    const state = {
                                        active: ch.active,
                                        blocked: ch.blocked,
                                        inSession: ch.inSession,
                                    };

                                    Object.keys(state).forEach((key) => {
                                        if (state[key]) {
                                            // customer+channel numbers
                                            state[key].customerNumber = state[key].customerNumber
                                                ? {
                                                    number: state[key].customerNumber.number,
                                                    provider: getCustomerNumberProviderString(
                                                        state[key].customerNumber.provider,
                                                    ),
                                                }
                                                : undefined;
                                            state[key].channelNumber = state[key].channelNumber
                                                ? {
                                                    number: state[key].channelNumber.number,
                                                    channel: getChannelProviderString(
                                                        state[key].channelNumber.channel,
                                                        MessagingChannel,
                                                    ),
                                                }
                                                : undefined;
                                        }
                                    });

                                    return state;
                                }),
                            };
                        }

                        resolve(result.data);
                    } catch (ex) {
                        reject(ex);
                    }
                },
                onError: (error) => reject(error),
            });
    });
};

/**
 * Adopt another customer's state
 * @param {Customer} customer
 * @param {Customer} otherCustomer
 * @returns {UpdateStatus}
 * @memberof Elarian.prototype
 */
const adoptCustomerState = async (customer, otherCustomer) => {
    const {
        getSocket,
    } = customerOptions;

    const socket = getSocket();

    if (!(customer instanceof Customer) || !(otherCustomer instanceof Customer)) {
        throw new Error('Invalid customer and/or otherCustomer');
    }

    let { customerId } = customer;

    if (!customerId) {
        const state = await customer.getState();
        customerId = state.customerId;
    }

    let cmd = new AdoptCustomerStateCommand()
        .setCustomerId(customerId);
    cmd = addOtherCustomer(cmd, otherCustomer);
    const req = new AppToServerCommand()
        .setAdoptCustomerState(cmd);

    return new Promise((resolve, reject) => {
        socket
            .requestResponse({
                data: Buffer.from(req.serializeBinary()),
            })
            .subscribe({
                onComplete: (value) => {
                    try {
                        const res = AppToServerCommandReply
                            .deserializeBinary(value.data)
                            .getUpdateCustomerState();

                        const result = {
                            status: res.getStatus(),
                            description: res.getDescription(),
                            customerId: (res.getCustomerId() || {
                                getValue: () => undefined,
                            }).getValue(),
                        };
                        if (!result.status) {
                            throw new Error(result.description);
                        }
                        resolve(result);
                    } catch (ex) {
                        reject(ex);
                    }
                },
                onError: (error) => reject(error),
            });
    });
};

