/**
 * Initiate a customer activity
 * @param {CustomerNumber} customerNumber
 * @param {ActivityChannelNumber} channelNumber
 * @param {string} sessionId
 * @param {string} key
 * @param {Object} properties
 * @returns {UpdateStatus}
 * @memberof Elarian.prototype
 */
const updateCustomerActivity = (customerNumber, channelNumber, sessionId, key, properties = {}) => {
    const socket = customerOptions.getSocket();

    let cmd = new CustomerActivityCommand();
    cmd = cmd
        .setSessionId(sessionId)
        .setKey(key)
        .setChannelNumber(
            new ActivityChannelNumber()
                .setNumber(channelNumber.number)
                .setChannel(getChannelProvider(channelNumber.channel, ActivityChannel)),
        );
    Object.keys(properties).forEach((name) => {
        cmd.getPropertiesMap().set(name, properties[name]);
    });
    cmd = addCustomerNumber(cmd, customerNumber);
    const req = new AppToServerCommand()
        .setCustomerActivity(cmd);

    return new Promise((resolve, reject) => {
        socket
            .requestResponse({
                data: Buffer.from(req.serializeBinary()),
            })
            .subscribe({
                onComplete: (item) => {
                    try {
                        const res = AppToServerCommandReply
                            .deserializeBinary(item.data)
                            .getCustomerActivity();
                        const result = {
                            status: res.getStatus(),
                            description: res.getDescription(),
                            transactionId: (res.getCustomerId() || {
                                getValue: () => undefined,
                            }).getValue(),
                        };
                        resolve(result);
                    } catch (ex) {
                        reject(ex);
                    }
                },
                onError: (error) => reject(error),
            });
    });
};

module.exports = {
    Customer,
    setup: (client, options) => {
        customerOptions = {
            ...options,
            client,
        };

        /* eslint-disable no-param-reassign */
        client.getCustomerState = getCustomerState;
        client.updateCustomerTag = updateCustomerTag;
        client.deleteCustomerTag = deleteCustomerTag;
        client.adoptCustomerState = adoptCustomerState;
        client.addCustomerReminder = addCustomerReminder;
        client.leaseCustomerAppData = leaseCustomerAppData;
        client.updateCustomerAppData = updateCustomerAppData;
        client.deleteCustomerAppData = deleteCustomerAppData;
        client.updateCustomerMetadata = updateCustomerMetadata;
        client.deleteCustomerMetadata = deleteCustomerMetadata;
        client.cancelCustomerReminder = cancelCustomerReminder;
        client.updateCustomerActivity = updateCustomerActivity;
        client.addCustomerReminderByTag = addCustomerReminderByTag;
        client.updateCustomerSecondaryId = updateCustomerSecondaryId;
        client.deleteCustomerSecondaryId = deleteCustomerSecondaryId;
        client.cancelCustomerReminderByTag = cancelCustomerReminderByTag;
        /* eslint-enable no-param-reassign */
        return client;
    },
};
